.footer {
    height: 400px;
    display: flex;
    align-items: center;
    background: var(--blue-color);
    background-image: url('../../img/footer/footer_bg.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    &__inner {
        width: 500px;
    }

    &__bottom {
        display: flex;
        justify-content: center;
        align-items: center;
        color: var(--light-color);
    }
}