.nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 40%;
    font-size: 20px;

    &__link {
        padding: 5px;
        border-radius: 5px;

        &:hover {
            background-color: var(--violet-color);

            transition: background-color 0.2s;
        }
    }

    &__btn {
        color: var(--light-color)
    }

    &__footer {
        position: relative;
        margin-bottom: 40px;
        width: 100%;

        &::after {
            content: "";
            position: absolute;
            bottom: -20px;
            display: block;
            margin: 15px auto 0;
            width: 100%;
            height: 5px;
            background-color: var(--violet-color);
        }
    }
}