.title {
    position: absolute;
    top: 30px;
    left: 0;
    right: 0;
    font-weight: 600;
    font-size: 75px;
    text-align: center;

    &-white {
        color: var(--light-color);
    }

    &-black {
        color: var(--dark-color);
    }

    &::after {
        content: "";
        display: block;
        margin: 15px auto 0;
        width: 200px;
        height: 5px;
        background-color: var(--violet-color);
    }
}