.show {
    position: relative;
    display: flex;
    align-items: center;
    height: 1080px;
    background-image: url('/img/show/pattern_bg.png');
    background-position: 25%;
    background-repeat: no-repeat;

    &__wrapper {
        display: flex;
        flex: 1;
        justify-content: space-between;
        align-items: center;
    }

    &__left {
        display: flex;
        flex-direction: column;
        width: 45%;
    }

    &__right {
        display: flex;
        justify-content: center;
        width: 45%;
    }

    &__name {
        margin-bottom: 15px;
        font-weight: 600;
        font-size: 50px;
    }

    &__info {
        margin-bottom: 20px;
        font-weight: 500;
        font-size: 30px;
        line-height: 140%;
        text-align: justify;

        &:last-child {
            margin-bottom: 0;
        }
    }
}