.front {
    display: flex;
    align-items: center;
    height: 1080px;
    background: var(--blue-color);
    background-image: url('/img/front/front_bg.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    color: var(--light-color);

    &__inner {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__suptitle {
        margin-bottom: 10px;
        font-weight: 400;
        font-size: 40px;
        line-height: 63px;
    }

    &__title {
        margin-bottom: 20px;
        font-weight: 700;
        font-size: 110px;
    }

    &__subtitle {
        margin-bottom: 150px;
        font-weight: 400;
        font-size: 35px;
    }
}