.contacts {
    position: relative;
    display: flex;
    align-items: center;
    height: 1150px;
    background-image: url('/img/contacts/pattern_bg.png');
    background-position: 50% 35%;
    background-repeat: no-repeat;

    &__inner {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__descr {
        margin-bottom: 50px;
        font-weight: 500;
        font-size: 40px;
        line-height: 49px;
    }

    &__row {
        display: flex;
        justify-content: start;
        align-items: center;
        margin-bottom: 40px;
    }

    &__icon {
        width: 30px;
    }

    &__text {
        margin: 0 0 0 30px;
        font-weight: 500;
        font-size: 30px;
        line-height: 49px;
        color: #000000;
    }
}