.tickets {
    position: relative;
    display: flex;
    align-items: center;
    height: 1459px;
    background-image: url('/img/tickets/pattern_bg.png');
    background-position: 75%;
    background-repeat: no-repeat;

    &__inner {
        display: flex;
        flex-direction: column;

        align-items: center;
    }

    &__calendar {
        margin-bottom: 40px;
        width: 900px;
    }

    &__info {
        margin-bottom: 40px;
        font-weight: 600;
        font-size: 30px;
        line-height: 43px;
        text-align: justify;
    }
}