.form {
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
    max-width: 700px;

    &__label {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    &__input {
        margin-bottom: 30px;
        padding: 20px;
        font-size: 25px;
        background-color: var(--violet-color);
        border-radius: 10px;

        color: var(--light-color);

        &::placeholder {
            color: var(--light-color);
            opacity: 1;
            /* Firefox */
        }
    }

    &__btn {
        max-width: 200px;
        padding: 15px;
        align-self: center;
    }
}