.place {
    position: relative;
    height: 1080px;
    display: flex;
    align-items: center;
    background: var(--blue-color);
    background-image: url('../../img/place/place_bg.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: var(--light-color);
    }

    &__left {
        width: 45%;
    }

    &__right {
        width: 45%;
    }

    &__img {
        border-radius: 10px;
    }

    &__row {
        margin-bottom: 50px;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &:last-child {
            margin-bottom: 0;
        }
    }

    &__icon {
        margin-right: 20px;
    }

    &__info {
        font-style: normal;
        font-weight: 500;
        font-size: 25px;
        line-height: 43px;
    }
}